import Noticia from './Noticia';

const Noticas = (props) => {
    return ( 
        <div className="row">
               {props.noticias.map(noticia => (
                    <Noticia 
                         key={noticia.url}
                         noticia={noticia}
                    />
               ))}
        </div>

     );
}
 
export default Noticas;